import React from 'react';
import './event-list.css'
import {connect} from 'react-redux'
import Event from '../Event/Event'

function EventList(props) {
    let eventList = props.eventList

    //const events =  useSelector(state => state.getAPIEvents)
    if(eventList) {
        return (
            <ul className="EventList">
                {eventList.map(event => 
                    <li className="Event" key={event.id}>
                        <Event event={event} />
                    </li>
                )}
            </ul>
        )
    } else {
        return (
            <div>
                loading!
            </div>
        )
    }
}
function mapStateToProps(state) {
    const {events} = state.getAPIEvents
    console.log(events)
    const {query} = state.filterEvents
    const {radius} = state.changeRadiusReducer
    const {lat} = state.getAPIEvents
    const {lon} = state.getAPIEvents
    let filteredEvents;
    function searchFilter(list, query) {
        filteredEvents = list.filter(event => {
            const lowerCaseEventName = event.name.toString().toLowerCase();
            const lowerCaseQuery = query.toString().toLowerCase();
            return lowerCaseEventName.includes(lowerCaseQuery)
        });
    }
    function radiusFilter(list, radius) {
        for(let i = 0; i < list.length; i++) {
            let event = list[i]
            if((event.group.lat > (lat + radius) || event.group.lat < (lat - radius)) || (event.group.lon > (lon + radius) || event.group.lon < (lon - radius)) ) {
                list.splice(i, 1)
            }
        }
    }
    switch(query | radius) {
        case query !== '' | radius :
            searchFilter(events, query)
            radiusFilter(filteredEvents, radius)
            break
        case query === '' | radius :
            filteredEvents = events
            radiusFilter(filteredEvents, radius)
            break
        default: filteredEvents = events
    }
    console.log('filter')
    console.log(filteredEvents)
    return {eventList: filteredEvents}

}
export default connect(mapStateToProps)(EventList)
//export default EventList;