import {combineReducers} from 'redux'

const getAPIEvents = (
    state = {
        lat: 0,
        lon: 0,
        loading: true,
        events: [],
        error: '',
        city: 'Stockholm, SE'
    }, 
    action) => {
    switch(action.type) {
        case 'FETCH_EVENTS_REQUEST':
            return {
                ...state,
                loading: true,
                lat: action.payload.lat,
                lon: action.payload.lon,
                city: `${action.payload.city}, ${action.payload.state}`
            }
        case 'FETCH_EVENTS_SUCCESS':
            //ADD A BACKGROUND IMAGE TO THE EVENT SINCE MEETUP DOES NOT PROVIDE ONE
            let listOfEvents = action.payload.events
            listOfEvents.forEach(event => {
                event.backgroundImageNumber = Math.floor(Math.random() * 11)
            });
            //---------------------------------------------------------------------
            return {
                ...state,
                loading: false,
                events: listOfEvents,
                error: '',
            }
        case 'FETCH_EVENTS_FAILURE':
            return {
                ...state,
                loading: false,
                events: [],
                error: action.payload,
            }
        default: return state
    }
}

const fetchAPISuggestions = (
    state = {
        loading: true,
        suggestions: [],
        error: ''
    }, 
    action) => {
    switch(action.type) {
        case 'FETCH_CITY_SUGGESTIONS_REQUEST':
            return {
                ...state,
                loading: true
            }
        case 'FETCH_CITY_SUGGESTIONS_SUCCESS':
            return {
                loading: false,
                suggestions: action.payload,
                error: ''
            }
        case 'FETCH_CITY_SUGGESTIONS_FAILURE':
            return {
                loading: false,
                suggestions: [],
                error: action.payload
            }
        case 'CLEAR_CITY_SUGGESTIONS':
            return {
                loading: false,
                suggestions: [],
                error: ''
            }
        default: return state
    }
}
const togglePopupReducer = (
    state = {
        open: false,
        popup: ''
    }, action) => {
    switch(action.type) {
        case 'TOGGLE_POPUP':
            return {
                open: !state.open,
                popup: action.payload
            }
        default: return state
    }
}
const toggleModalReducer = (
    state = {
        open: false,
        modal: {}
    }, action) => {
        switch(action.type) {
            case 'TOGGLE_MODAL':
                return {
                    open: !state.open,
                    modal: action.payload
                }
        default: return state
        }
    }
const changeRadiusReducer = (
    state = {
        display: '10 miles',
        radius: (10 * 1.44) / 100
    }, action) => {
        switch(action.type) {
            case 'CHANGE_RADIUS':
                return {
                    display: action.payload.display,
                    radius: action.payload.radius
                }
            default: return state
        }
}

const counterReducer = (state = 0, action) => {
    switch(action.type) {
        case 'INCREMENT':
            return state + 1
        case 'DECREMENT':
            return state - 1
        default: return state
    }
}

const loggedReducer = (state = false, action) => {
    switch(action.type) {
        case 'SIGN_IN':
            return !state 
            default: return state   
    }
}
const closeOpeningModalReducer = (state = true, action) => {
    switch(action.type) {
        case 'CLOSE_OPENING_MODAL':
            return !state
        default: return state
    }
}
const filterEvents = (state = {query: ''}, action) => {
    switch(action.type) {
        case 'FILTER_EVENTS':
            return state = {query: action.payload }
            default: return state
    }
}

const rootReducer = combineReducers({
    counterReducer,
    loggedReducer,
    fetchAPISuggestions,
    getAPIEvents,
    togglePopupReducer ,
    changeRadiusReducer,
    toggleModalReducer,
    closeOpeningModalReducer,
    filterEvents
})

export default rootReducer;